<template>
    <div >
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">数据库类型：</span>
               <Input v-model="SQLType" disabled placeholder="数据库类型" class="input-width"></Input>
            </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">主机：</span>
            <Input v-model="host" placeholder="主机" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">端口：</span>
            <Input v-model="port" placeholder="端口" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">数据库名：</span>
            <Input v-model="SQLName" placeholder="数据库名" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">用户名：</span>
            <Input v-model="userName" placeholder="用户名" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">密码：</span>
            <Input type="password" v-model="passWord" placeholder="密码" class="input-width"></Input>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item" style="float:right">
            <Button type="primary" @click="exportInfo">导出</Button>
          </div>
        </Col>
      </Row>
    </div>
</template>
<script>
    export default {
      data(){
        return{
          SQLType:'PostgreSQL',
          host:'10.100.20.211',
          port:'5432',
          SQLName:'query',
          userName:'postgres',
          passWord:'',
        }
      },
    mounted() {
      this.$nextTick(function () {
        })
    },
      components: {

      },
      //导出数据
      methods: {
        exportInfo(){
          let that=this
          this.$Modal.confirm({
            title: '提示',
            content: `确定导出吗？`,
            onOk: ()=>{
              this.exportOk()
            }
          })
        },
        //导出数据
        exportOk(){
          let objData={
           /* host:"172.16.20.212",
            port:"5432",
            db:"query",
            user:"postgres",
            password:"postgres"*/
            host:this.host,
            port:this.port,
            db:this.SQLName,
            user:this.userName,
            password:this.passWord
            }
            console.log(objData)
          let jsonData = protocol.getJsonParams(protocol.exportDB2XML, objData);
          http.ajax('', jsonData, true, (data) => {
            window.location.href=loadUrl+data.excel_url
          })
        },

      }

    }
</script>

<style scoped>
  @import '../../css/common.css';
</style>



